<template>
	<div id="app">

		<loading id="loading" v-if="$store.state.loading"></loading>
		<app-header v-if="$store.state.AppHeader"></app-header>

			<router-view class="content"></router-view>

		
		<app-footer v-if="$store.state.AppFooter"></app-footer>
	</div>
</template>

<script>
	import loading from '../components/loading.vue'
	import AppHeader from './header.vue'
	import AppFooter from './footer.vue'
	import store from '../plugins/vuex.js'
	export default {
		name: 'App',
		components: {
			loading,
			AppHeader,
			AppFooter
		},
		watch: {
			$route: {
				handler(nextValue) {
					let path = nextValue.path;
					if (/home|follow|column/.test(path)) {
						this.$store.commit('AppHeader',true)
						this.$store.commit('AppFooter',true)
						
						
						// this.$root.AppHeader = this.$root.AppFooter = true;
					}
					if (/user/.test(path)) {
						// this.$root.AppFooter = true;
						// this.$root.AppHeader = false;
						
						this.$store.commit('AppFooter',true)
						this.$store.commit('AppHeader',false)
					}
					if (/detail|login|reg/.test(path)) {
						// this.$root.AppFooter = this.$root.AppHeader = false;
						
						
						this.$store.commit('AppHeader',false)
						this.$store.commit('AppFooter',false)
						
					}
				},
				immediate: true

			}
		}

	}
</script>

<style scoped>
	#loading {
		position: fixed;
		left: 50%;
		top: 50%;
		margin-left: -45px;
	}
	.content{
		margin-bottom: 0.5rem;
	}
</style>
