import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter);

import home from '../pages/home.vue'


// import follow from '../pages/follow.vue'
const follow = ()=>import(/* webpackChunkName: "groupname-home" */'../pages/follow.vue');
const detail = ()=>import(/* webpackChunkName: "groupname-home" */'../pages/detail.vue');
const column = ()=>import(/* webpackChunkName: "groupname-home" */'../pages/column.vue');
const NoPage = ()=>import(/* webpackChunkName: "groupname-home" */'../pages/no-page.vue');
const login = ()=>import(/* webpackChunkName: "groupname-home" */'../pages/login.vue');
const reg = ()=>import(/* webpackChunkName: "groupname-home" */'../pages/reg.vue');
const user = ()=>import(/* webpackChunkName: "groupname-home" */'../pages/user.vue');
const favorites = ()=>import(/* webpackChunkName: "groupname-home" */'../pages/Favorites.vue');
// import detail from '../pages/detail.vue'
// import column from '../pages/column.vue'
// import NoPage from '../pages/no-page.vue'
// import login from '../pages/login.vue'
// import reg from '../pages/reg.vue'
// import user from '../pages/user.vue'
// import favorites from '../pages/Favorites.vue'



let router = new VueRouter({
	mode:'history'
});

router.addRoutes([
	{path:'/home',component:home},
	{path:'/follow',component:follow},

	
	{path:'/column',component:column},
	
	{path:'/login',component:login},
	{path:'/reg',component:reg},
	{path:'/user',component:user},
	{path:'/detail/:_id',component:detail,name:'detail'},
	{path:'/favorites',component:favorites},
	{path:'/',redirect:'/home'},
	{path:'*',component:NoPage}
]	)
export default router;