<template>
	<div class="cell_box">
		<div class="data" @click="getDetail">
			<h2><span v-show="index !== null">{{index + 1}}.</span>{{data.title}}</h2>
			<p>{{data.des}}</p>
		</div>
		<div class="slot">
			<slot></slot>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'cell',
		props: {
			data: {
				type: Object,
				required: true,
				default: function() {
					return {
						title: '醉',
						des: '酒不醉人人自醉'
					}
				}
			},
			to: {
				type: [String, Object],
				default: ''
			},
			url: {
				type: String,
				default: ''
			},
			index: {
				type: Number,
				default: null
			}
		},
		methods: {
			getDetail() {
				if (this.to) {
					this.$router.push(this.to)
				}
				if (this.url) {
					window.open(this.url, '_black')
				}
			}
		}
	}
</script>

<style scoped>
	.cell_box {
		color: #494d4d;
		padding: 0.2rem 0;
		border-bottom: 1px dashed #ccc;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.cell_box h2 {
		max-height: 0.9rem;
		font-size: 0.33rem;
		overflow: hidden;
	}

	.cell_box p {
		max-height: 1.8rem;
		margin-top: 0.05rem;
		overflow: hidden;
		margin-left: 0.08rem;
	}

	.cell_box {
		clear: both;
		/* margin-bottom:1rem; */
	}

	.slot {
		float: right;

	}
</style>
