import axios from '../../plugins/axios.js'
let state ={
	follow:[]
}
let actions={
	follow:({commit},payload)=>{
		axios({
			url: '/news/new',
			params: {
				_limit: 15
			}
		}).then(res=>{
			commit('follow',res.data.data)
		})
	}
}
let mutations={
	follow:(state,payload)=>{
		state.follow = payload
	}
}

export default {
	namespaced:true,
	state,actions,mutations
}