import axios from '../../plugins/axios.js'
let state ={
	column:[]
}
let actions={
	column:({commit},payload)=>{
		axios({
			url: '/news/new',
			params: {
				_limit: 15
			}
		}).then(res=>{
			commit('column',res.data.data)
		})
	}
}
let mutations={
	column:(state,payload)=>{
		state.column = payload
	}
}

export default {
	namespaced:true,
	state,actions,mutations
}