<template>
	
	<div :style="{
		background:bgColor,
		borderWidth:borderWidth + 'px',
	}" class="nav">
		<ul>
			<li @click="goBack" :class="cptArrow"> </li>
		</ul>
	</div>
</template>

<script>
	export default {
		name: 'nav-bar',
		props: {
			bgColor: {
				type: String,
				default: '#fff'
			},
			borderWidth: {
				type: Number,
				default: 0
			},
			arrow: {
				type: String,
				default: 'gray'
			}
		},
		methods: {
			goBack() {
				this.$router.go(-1)
			}
		},
		computed: {
			cptArrow() {
				return 'default' ? 'default' : 'gray'
			}
		}
	}
</script>

<style scoped>
	.nav {
		width: 100%;
		position: fixed;
		top: 0;
		left: 0;
		background: #fff;
		border-bottom: 1px solid #e8eaec;
		z-index: 99;
	}

	.nav ul {
		width: 6.4rem;
		height: 0.45rem;
		padding-top: 0.15rem;
		margin: 0 auto;
	}

	.nav ul li {
		width: 0.29rem;
		height: 0.31rem;
		/* background:url(./assets/img/history.png) no-repeat 0 0; */
		background-size: 100%;
		margin: 0 0 0 0.38rem;
	}

	.nav ul li.gray {
		background-image: url(../assets/img/history1.png)
	}

	.nav ul li.default {
		background-image: url(../assets/img/history.png)
	}
</style>
