<template>
	<div>
		<div class="banner_box">
			<swiper :data="banner" collectionName='new' :autoplay="2000"></swiper>
		</div>
		<div class="cell_box">
			<cell v-for="(n,index) in msg" :data="n" :index="index" :key="n._id"
				:to="`/detail/${n._id}?collectionName=page`">
				<a href="javascript:" @click="favorites(n._id)">收藏</a>
			</cell>
		</div>

	</div>
</template>

<script>
	import swiper from '../components/swiper.vue'
	import cell from '../components/cell.vue'
import Vue from 'vue';
import { Toast } from 'vant';

Vue.use(Toast);
	
	export default {
		name: 'home',
		components: {
			swiper,
			cell,

		},
		data() {
			return {
				banner: [],
				msg: [],
			}
		},
		mounted() {
			if(!localStorage.getItem('user')){
				this.$router.push('/login')
			}else{
				this.$axios.all([
					this.$axios({url: '/news/new',params: {_limit: 15}}),
					this.$axios({url: '/news/banner',params: {_limit: 3}})
				]).then(this.$axios.spread((news,banner)=>{
					if(news.data.err === 1){
						this.$router.push('/login')
					}else{
						this.msg = news.data.data;
					}
					
					banner.data.data.map((item, index) => {
						if (!item.banner.includes('http')) {
									
							item.banner = this.$baseUrl + item.banner;
						}
					})
					this.banner = banner.data.data
				}))
				
			}
			
		},
		methods:{
			favorites(id){
				let newsId = id;
				let _id = JSON.parse(localStorage.getItem('user')).data._id;
				this.$axios({
					url:'/favorites',
					method:'post',
					params:{
						_id:_id,
						newsId:newsId
					}
				}).then(res=>{
					if(res.data.err === 0){
						Toast.success(res.data.msg);
					}else{
						Toast.fail(res.data.msg);
					}
				})
			}
		}

	}
</script>

<style scoped>
	.cell_box {
		padding: 0.15rem;
	}

</style>
