import axios from '../../plugins/axios.js'
import {baseUrl} from '../../config';
let state ={
	detail:{}
}
let actions={
	detail:({state,commit},{collectionName,_id})=>{

		axios({
			url:`news/${collectionName}/${_id}`
		}).then(res=>{
			if(!res.data.data.author_icon.includes('http')){
				res.data.data.author_icon = baseUrl + res.data.data.author_icon;
			}

			commit('detail',res.data.data)
		})
	}
}
let mutations={
	detail:(state,payload)=>{
		state.detail = payload
	}
}

export default {
	namespaced:true,
	state,actions,mutations
}