<template>
	<div class="btn" :style="{backgroundColor: backgroundColor,color:color}"  @click="show" >{{texts}}</div>
</template>

<script>
	export default {
		name:'btn',
		props:{
			texts:{
				type:String,
				default:'按 钮'
			},
			backgroundColor:{
				type:String,
				default:'#F66427'
			},
			color:{
				type:String,
				default:'#fff'
			}
			
		},
		methods:{
			show(e){
				this.$emit('click',e);
			},

		}
	}
</script>

<style scoped>
	.btn{
		background: #F66427;
		font-size: 0.28rem;
		line-height: 0.56rem;
		color: #fff;
		border-radius: 8px;
		text-align: center;
		display: block;
	}
</style>
